:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

body {
  margin: 0;
  padding-bottom: 50px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.18;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
}

.header {
  z-index: 1;
  background-color: #87ceeb;
  padding-top: 10px;
  padding-bottom: 10px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  box-shadow: 0 10px 21px -5px rgba(0, 0, 0, .55);
}

.search-form {
  justify-content: center;
  display: flex;
}

.search-input {
  height: 36px;
  min-width: 250px;
  border: none;
  border-radius: 4px;
  font-size: 18px;
}

.submit {
  height: 38px;
  width: 38px;
  border: none;
  border-radius: 4px;
  margin-left: 13px;
}

.load-more-div {
  text-align: center;
  height: 150px;
  width: 300px;
  color: #fff;
  cursor: pointer;
  background-color: #87ceeb;
  border-radius: 50px;
  align-items: center;
  margin: 0 auto;
  font-size: 30px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 10px 21px -5px rgba(0, 0, 0, .55);
}

.is-hidden {
  display: none;
}

.gallery {
  max-width: 1400px;
  grid-row-gap: 100px;
  grid-column-gap: 15px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin: 50px auto 150px;
  padding: 0;
  list-style: none;
  display: grid;
}

.photo-card {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

a {
  max-width: 320px;
  color: #000;
  text-decoration: none;
}

.gallery-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery-img:hover {
  transform: scale(1.02);
}

.info {
  justify-content: space-around;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.info-item {
  text-align: center;
  flex-direction: column;
  line-height: 1.8;
  display: flex;
}

